import React from 'react'
import { DrinkItem } from '../components/DrinkItem';
export const Drinks = (props) => {
    return (
        <div>
            {/* {<p>Length is {props.drinks.length} </p>} */}
           {props.drinks.length===0?<p>No Drinks Found
           </p>:<h3>Find {props.drinks.length} Drinks Variety</h3> }

           {props.drinks.map((drink,index)=>{
               return <DrinkItem key={index} drink={drink} />
           })}

        </div>
    )
}
