import React from 'react'
import { PizzaItem } from '../components/PizzaItem';
export const Pizza = (props) => {
    return (
        <>
             <h1>hello Pizza</h1>
            {props.pizza.length===0?<p>No Pizza Found</p>:<h3>Find {props.pizza.length} Pizza</h3> }
            {props.pizza.map((piza,index)=>{
                return(<PizzaItem key={index} piza={piza}/>);
            })}

           
        </>
    )
}
