import React from 'react'
import { NavLink } from 'react-router-dom';
import "./Sidebar.css"
export const Navebar = () => {
    return (
        <div className="link">
            <ul>
                <li>
            
             <NavLink exact activeClassName="active_class" to="/" > Login</NavLink>
             </li>
             <li>
             <NavLink exact activeClassName="active_class" to="/pizza" >Pizza</NavLink>
             </li>
             <li>
             <NavLink exact activeClassName="active_class" to="/drinks" >Drinks</NavLink>
             </li>
             </ul>

        </div>
    )
}
