import React,{useState,useEffect} from 'react'
import './Sidebar.css';
import { Login } from './Login';
import { Pizza } from './Pizza';
import { Route, Switch } from 'react-router-dom';
import { Navebar } from './Navebar';
import { Drinks } from './Drinks';
import { IteminCart } from '../components/IteminCart';
import { getProduct } from '../utils/Ajax';
import { getDrinks } from '../utils/Ajax2';
import { ItemContext } from '../utils/Context';
export const Sidebar = () => {

    
        const[products,setProducts]=useState([]);
        const [drinks, setDrinks] = useState([]);
        const [totalIteminCartCount, updateCount] = useState(0);

        //let totalIteminCartCount=0;
        const  updateItemInCart=(val)=>{
     
         // totalIteminCartCount +=val;
          updateCount(totalIteminCartCount+val);
           
          console.log("Total Value is ", totalIteminCartCount);
  
        }

        useEffect(() => {
          const promise= getProduct();
             promise.then(response=>{
                 console.log("Data is ",response)
                console.log("data",response.data['mobiles'])
              setProducts(response.data['mobiles']);
            }).catch(err=>{
              console.log("Server Err",err);
            })
        }, [])

        useEffect(() => {
            const promise=getDrinks();
            promise.then(response=>{
                console.log("Dirink data is",response);
                setDrinks(response.data['drinks']);
                //console.log("Drink ",drinks);
            }).catch(err=>{
                console.log("Erroer is ",err)
            })
            
        }, [])


    return (
        <>
        <ItemContext.Provider value={{IteminCartCount:totalIteminCartCount , updateItemCount:updateItemInCart}}>
            <IteminCart />
            <div className="sidebar">
                <div className="side">
                    <Navebar/>
                </div>
                <div className="contenet">
                    <Switch>
                        <Route exact path='/' component={Login} />
                        <Route path='/pizza'  component={()=>{
                            return <Pizza pizza={products}/>
                        }} />
                        <Route path='/drinks' component={()=>{
                            return <Drinks drinks={drinks} />
                        }} />
                    </Switch>
                </div> 
            </div>
            </ItemContext.Provider>
        </>
    )
}
