import React from 'react'
import { Button } from './AddtoCart';
export const DrinkItem = ({drink}) => {
    return (<>
        <div className="cards">
            <div className="card">
                {<p>Drink ID :{drink.idDrink}</p>}
                <img src={drink.strDrinkThumb} alt={drink.strDrinkThumb}></img>
                {<h4>{drink.strDrink} &nbsp;&nbsp;</h4>}
                <Button/>
            </div>
        </div>
        </>
    )
}
